cc.Class({
    extends: cc.Component,

    properties: {
        map:{
            default: null,
            type: cc.Node,
        },
    },

    onLoad () {
        let map = this.map.getComponent('boardinfo');
    },

    start () {

    },

    update (dt) {
        let map = this.map.getComponent('boardinfo');
        this.node.height = map.countreddead*map.chesssize+100;
    },
});
